import Vue from "vue"
import Vuex from "vuex"
// Vuex
Vue.use(Vuex);


// 
// computedstatedata
const state={
	date:new Date()+"",
	num:0,
	// 
	orderList:[],
	done:[],
	notdone:[],
	cancel:[],
	username:[],
	time:"15:00",
	timer:""
};

// state
// mutation
const mutations={
	// state
	// state,
	setDate(state){
		state.date=new Date()+"";
	},
	addNum(state){
		state.num++;
	},
	addPro(state,pro){
		var flag=true;
		for(var n=0;n<state.orderList.length;n++){
			if(pro.name===state.orderList[n].name){
				state.orderList.splice(n,1,pro);
				flag=false;
			}
		}
		if(flag){
			state.orderList.push(pro);
		}
	},
	minusPro(state,pro){

		for(var n=0;n<state.orderList.length;n++){

			if(pro.name===state.orderList[n].name){
				state.orderList.splice(n,1,pro);
				if (pro["is_featured"]===0) {
					state.orderList.splice(n,1);
				};
			}
		};

	},
	setTime(state,pro){
		var count=900;
		if(pro===1){
			clearInterval(state.timer);
			  state.timer=setInterval(changenum,1000);
			  function changenum(){
			    count--;
			    if(Math.floor(count/60)>=10){
			      if(count%60>=10){
			        state.time=Math.floor(count/60)+":"+count%60;  
			      }else{
			       	state.time=Math.floor(count/60)+":0"+count%60;
			      }
			    }else{
			         if(count%60>=10){
			            state.time="0"+Math.floor(count/60)+":"+count%60;  
			          }else{
			            state.time="0"+Math.floor(count/60)+":0"+count%60;
			          }
			    }
			  }
		}else if(pro===0){
			clearInterval(state.timer);
		}
	}
};
// mutattionsstatemutationactionsstate
const actions={
	// mutationsmutaitionstate
	// store
	set_Date(store){
		setInterval(function(){
			// actionsmutationcommit,actions
			store.commit("setDate");

		},1000)

	},
	// storekey
	add_Num({commit}){
		commit("addNum")
	},
	add_Pro({commit},pro){

		commit("addPro",pro);
		//  
	},
	minus_Pro({commit},pro){
		commit("minusPro",pro)
	},
	set_Time({commit},pro){
		commit("setTime",pro)
	}
};
const getters={
// 
	total(state){
		var total=0;
		for(var n=0;n<state.orderList.length;n++){
			total+=state.orderList[n]["is_featured"]*state.orderList[n].specfoods[0].price;
		}
		return total;
	}
};
export default new Vuex.Store({
	state,mutations,actions,getters
});
// store


