<template>
  <div id="p_box">
    <header class="po_header">
      <div class="po_header_inner">
        <div class="po_back" @click="goback"></div>
        <h1 class="po_checkorder"></h1>
        <span class="po_newadd"></span>
      </div>
    </header>
    <form action="" class="po_addform">
      <img src="../assets/search.png" alt="">
      <input type="text" class="po_addinput" placeholder="" id="search">
    </form>
    <section class="po_curradd">
      <h4></h4>
      <div class="po_adds">
        <span class="po_adds1"></span>
        <span class="po_dingwei">
          <i></i>
          <span></span>
        </span>
      </div>
    </section>
    <section class="po_getadd">
      <h4></h4>
      <div class="po_getadds">
        <p>
          <span class="po_addname"></span>
          <span class="po_addsex"></span>
          <span class="po_addphone">13678965678</span>
        </p>
        <p class="po_adddown"></p>
      </div>
    </section>
    <section id="mapbox">
      <div id="map"></div>
      
    </section>
  </div>
</template>

<script>
export default {
  name: 'poi',
  data () {
    return {
      poi:{}
    }
  },
  created(){
    bus.$emit("isHide",true);
     this.$http("../../static/poi.json").then((res)=>{
        this.poi=res.data;
    });
  },
  methods:{
    goback(){
        window.history.back();
        bus.$emit("isHide",false);
    }
  },
  mounted(){
    // 
    var map = new AMap.Map('map',{
        zoom: 15,
        pitch:80,
        expandZoomRange:true,
        zooms:[3,20]
      });
    // 
    var scale =new AMap.Scale(),
    overView=new AMap.OverView(),
    toolBar=new AMap.ToolBar();
    // 
    map.addControl(scale);
    map.addControl(overView);
    map.addControl(toolBar);
    // h5navigator.geolocation
    if(navigator.geolocation){
      // getlocation.getCurrentPosition
      navigator.geolocation.getCurrentPosition(function  (poi) {
        var marker=new AMap.Marker({
          map:map,
          // 
          position:[poi.coords.longitude,poi.coords.latitude]
        });   
    },function(error){
      console.log(error)
    });
    }

  }


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/Css/Poi.css'
</style>


